import '@k2oss/k2-broker-core';
import 'randomstring';

metadata = {
    systemName: "com.k2.randomstring",
    displayName: "Random Unique String Broker",
    description: "An example broker that returns a random, unique string as text formated as alphabetic, numeric or alphanumeric, using a Generate Access Code method.",
    //configuration values (service keys)
    "configuration": {
        "TextFormat": {
            displayName: "Return Text Format.",
            type: "string",
            value: "alphanumeric"
        },
    },
};

ondescribe = async function(): Promise<void> {
    postSchema({
        objects: {
            "randomstring": {
                displayName: "RandomString",
                description: "Random string to return to user",
                properties: {
                    "format": {
                        displayName: "Format",
                        type: "string"
                    },
                },
                methods: {
                    "generateCode": {
                        displayName: "Generate Access Code",
                        type: "create",
                        parameters: {
                            "pCharacters" : { displayName: "param1", description: "The number of characters 5-8 in the returned string", type: "number" }
                        },
                        requiredParameters: [ "pCharacters" ],
                        outputs: [ "randomstring" ]
                    },
                }
            }
        }
    });
}

onexecute = async function getARandomString(objectName, methodName, parameters, properties, configuration):  Promise<void> {
        var randomstring = require("randomstring");
    
        randomstring.generate({
            length: parameters["pCharacters"],
            charset: properties["format"]
    })
    //console.log(randomstring)
}